// Wait for the DOM to load before running scripts
document.addEventListener('DOMContentLoaded', () => {
    // Toggle password visibility for signup
    const togglePasswordSignUp = document.getElementById('togglePasswordSignUp');
    const passwordInputSignUp = document.getElementById('password');
    if (togglePasswordSignUp) {
        togglePasswordSignUp.addEventListener('click', function () {
            const type = passwordInputSignUp.getAttribute('type') === 'password' ? 'text' : 'password';
            passwordInputSignUp.setAttribute('type', type);
            this.classList.toggle('fa-eye-slash');
        });
    }

    // Toggle password visibility for confirm password
    const toggleConfirmPassword = document.getElementById('toggleConfirmPassword');
    const confirmPasswordInput = document.getElementById('confirmPassword');
    if (toggleConfirmPassword) {
        toggleConfirmPassword.addEventListener('click', function () {
            const type = confirmPasswordInput.getAttribute('type') === 'password' ? 'text' : 'password';
            confirmPasswordInput.setAttribute('type', type);
            this.classList.toggle('fa-eye-slash');
        });
    }

    // Handle signup
    document.getElementById('signupBtn').addEventListener('click', function (event) {
        event.preventDefault(); // Prevent form submission
        handleSignup();
    });

    // Handle login
    document.getElementById('loginForm').addEventListener('submit', handleLogin);

    // Function to analyze student profile
    window.analyzeProfile = async function () {
        const name = document.getElementById('name').value;
        const school = document.getElementById('school').value;
        const gpa = document.getElementById('gpa').value;
        const gpa_scale = document.getElementById('gpa_scale').value;
        const sat = document.getElementById('sat').value;
        const major = document.getElementById('major').value;
        const extracurriculars = document.getElementById('extracurriculars').value;
        const leadership = document.getElementById('leadership').value;
        const community_service = document.getElementById('community_service').value;
        const work_experience = document.getElementById('work_experience').value;

        try {
            const response = await fetch('http://127.0.0.1:5000/predict', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    GPA: parseFloat(gpa),
                    SAT: parseInt(sat, 10),
                    Extracurriculars: parseInt(extracurriculars, 10),
                    Leadership: parseInt(leadership, 10),
                    Community_Service: parseInt(community_service, 10),
                    Work_Experience: parseInt(work_experience, 10)
                })
            });

            if (!response.ok) throw new Error('Network response was not ok');
            const result = await response.json();
            document.getElementById('result').innerText = `Admission Chance: ${result.admission_chance ? 'Accepted' : 'Rejected'}`;
        } catch (error) {
            console.error('Error:', error);
            document.getElementById('result').innerText = 'Error: Unable to analyze profile.';
        }
    };
});

